import blogService from '../services/blogs'

const blogReducer = (state = [], action) => {
    console.log('state now: ', state)
    console.log('action', action)
    switch(action.type){
      case 'LIKE':{
        return state.map(n => n.id !== action.data.id ? n : action.data)
      }
      case 'DELETE':{
        return state.filter(n => n.id !== action.data.id)
      }
      case 'ADD':{
        return action.data
      }
      case 'INIT_BLOG':
        return action.data
      default:
    return state
    }
  }
  
  export const initializeBlog = () => {
    return async dispatch => {
      const items = await blogService.getAll()
      dispatch({
        type: 'INIT_BLOG',
        data: items,
      })
    }
  }
  
  export const likeBlog = (blog) => {
    return async dispatch => {
      const blogToUpdate = {...blog, likes: blog.likes + 1}
      const likedBlog = await blogService.updateBlog(blogToUpdate)
      const items = await blogService.getAll()
      dispatch({
        type: 'INIT_BLOG',
        data: items,
      })
    }
  }
  
  export const createBlog = (content) =>{
    return async dispatch =>{
      await blogService.create(content)  
      const item = await blogService.getAll()
      dispatch({
        type: 'ADD',
        data: item,
      })
    }
  }

  export const deleteBlog = (blog) =>{
      return async dispatch =>{
        const deletedBlog = await blogService.remove(blog)
        dispatch({
          type: 'DELETE',
          data: blog
        })
      }
  }
  
  export default blogReducer