import React, { useState, useEffect,useRef } from 'react'
import BlogList from './components/BlogList'
import Notification from './components/Notification'
import UserAuthentication from './components/Authentication'
import BlogForm from './components/BlogForm';
import Togglable from './components/Togglable';
import {notification} from './reducers/notificationReducer'
import blogService from './services/blogs'
import {setUser} from './reducers/userReducer'
import {removeUser} from './reducers/userReducer'
import {initializeBlog} from './reducers/blogReducer'
import { connect } from 'react-redux'
import { useDispatch } from 'react-redux'
import './App.css'

const App = (props) => {
  const dispatch = useDispatch()

  const blogFormRef = useRef()

  useEffect(() => {
    dispatch(initializeBlog())
  }, [dispatch])

  useEffect(() => {
    const loggedUserJSON = window.localStorage.getItem('loggedBlogappUser')
    if (loggedUserJSON) {
      const user = JSON.parse(loggedUserJSON)
      props.setUser(user)
      blogService.setToken(user.token)
    }
  }, [])

  const logout = () =>{
    props.removeUser();
    window.localStorage.setItem(
      'loggedBlogappUser', ''
    ) 
  }

  return (
    <div>
      <h1>Recommended Blog Posts</h1>

      <Notification/>
      
      {props.user === null || props.user =='' ?
      (
        <UserAuthentication/>
      ) 
      :(
      <div>
        <div className='app_user'>
          <p>{props.user.username} logged-in</p>
          <button type='button' onClick={logout} className='logout_button'>Log out</button>
        </div>
        <Togglable className='addNewBlog' buttonLabel='Add new blog' ref={blogFormRef}>
          <BlogForm></BlogForm>
        </Togglable>
      </div>)
    }
    <BlogList/>
    </div>
  )
}

const mapStateToProps =(state)=>{
  return {user:state.blogUser}
}

const mapDispatchToProps = {
  notification, removeUser,setUser
}

const ConnectApp = connect(mapStateToProps,mapDispatchToProps)(App)
export default ConnectApp