import React,{useState} from 'react';
import './Blog.style.css';
import {likeBlog} from '../reducers/blogReducer'
import {notification} from '../reducers/notificationReducer'
import {deleteBlog} from '../reducers/blogReducer'
import { connect } from 'react-redux';
import Blog from './Blog'

const BlogList = (props) => {

  return(
  <div className='blog-list'>
    {props.blogs.map(blog=>(
      <Blog id={blog.id} blog = {blog} like={props.likeBlog} notification={props.notification} del={props.deleteBlog} user={props.user}/>
    ))}
  
  </div>
  )
}

const mapStateToProps =(state)=>{
  const sortBlogs = state.blogs.sort(function(a,b){
    return b.likes - a.likes
  })
  return {blogs: sortBlogs,user:state.blogUser}
}

const mapDispatchToProps ={
  likeBlog,notification,deleteBlog
}

export default connect(mapStateToProps,mapDispatchToProps)(BlogList)
